import styled from "styled-components";
import { InnerWrap, Text, Title2 } from "../../assets/styles/component";

const SWhyTry = styled.section`
  background: radial-gradient(
      56.92% 42.11% at 111.57% 109.48%,
      #06a5d8 20.47%,
      rgba(0, 51, 233, 0.637795) 59.04%,
      rgba(41, 1, 157, 0) 100%
    ),
    radial-gradient(
      71.73% 87.75% at -17.08% -20.39%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #29009c;
  border-radius: 106px;
  padding: 150px 0;

  & > div {
    display: flex;
    align-items: center;
    flex-direction: column;

    & > p {
      text-align: center;
      margin-bottom: 42px;
    }
  }

  @media (max-width: 1000px) {
    padding: 70px 0;
    border-radius: 60px;
  }
`;

const Title = styled(Title2)``;

const WhyTryList = styled.div`
  display: flex;
  gap: 24px;
  margin: 100px 0;
  width: 100%;

  @media (max-width: 1300px) {
    margin: 80px 0 50px 0 ;
  }
`;

const WhyTryReason = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(33% - 12px);
  padding: 99px 90px 68px 90px;
  gap: 71px;
  box-sizing: border-box;
  background: #ffffff22;
  border-radius: 77px;

  p {
    text-align: center;
    line-height: 150%;
  }

  img {
    width: 100%;
  }

  @media (max-width: 1300px) {
    border-radius: 30px;
    gap: 25px;
    padding: 43px 5% 29px 5%;

    p {
      font-size: clamp(10px, 1.5vw, 20px);
    }
  }
`;

const Button = styled.button`
  border: 0;
  cursor: pointer;
  font-weight: 400;
  font-size: clamp(10px, 3vw, 26px);
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.01em;
  color: #29009c;
  background: #ffffff;
  outline: 10px solid rgba(255, 255, 255, 0.1);
  border-radius: 73px;
  padding: 33px 96px;
  transition: 0.25s ease-in-out;

  &:hover {
    transform: scale(1.01);
    outline: 14px solid rgba(255, 255, 255, 0.1);
  }

  @media (max-width: 1300px) {
    padding: 27px 60px;
  }

  @media (max-width: 1000px) {
    padding: 23px 60px;
  }
`;

const WhyTry = ({ whytrylist, language }) => {
  return (
    <SWhyTry>
      <InnerWrap>
        <Title>
          Почему именно <br /> SocialStream?
        </Title>

        <WhyTryList>
          {whytrylist.map((whytry, index) => (
            <WhyTryReason key={index}>
              <img src={whytry.img} alt={"Причина " + index}/>
              <Text size="20">{whytry.text}</Text>
            </WhyTryReason>
          ))}
        </WhyTryList>

        <Text size="26">
          Узнайте, какой формат подойдет <br /> именно для вашего продукта:
        </Text>
        <Button>Начать общение</Button>
      </InnerWrap>
    </SWhyTry>
  );
};

export default WhyTry;
